@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@1&family=EB+Garamond&family=Montserrat:ital,wght@1,500&family=Zen+Kaku+Gothic+Antique&display=swap');

body {
  margin: 0;
  font-family: 'Zen Kaku Gothic Antique', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.calender{
  width:85%;
  margin: 5% auto;
}

.fc-theme-standard td, .fc-theme-standard th {
  height: 100px;
}

.fc-theme-standard th{
  height: 30px;
  background-color: skyblue;
  color: white;
}
.fc-day-sun{
  background-color:rgba(242, 195, 195,0.5);
}
.fc-day-sat{
  background-color: rgba(192, 205, 238, 0.5);
}

.fc-daygrid-event-harness{
  text-align: center;
  padding: 10px;
}

/*
.fc-daygrid-event-harness a{
  display: block;
  height: 100%;
}
*/

.fc-event-title{
  font-size: 15px;
  white-space: normal;
}

.fc-event:hover{
  opacity: 0.6;
  cursor: pointer;
}

.fc-button:hover{
  opacity: 0.8;
}

.OverlayModal {
  position:fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 100;
}
.Modal {
    position:absolute;
    top: 25%;
    left: 25%;
    right: 25%;
    bottom: 25%;
    border: 1px solid #ccc;
    background: #fff;
    overflow: auto;
    border-radius: 4px;
    outline: none;
    padding: 20px;

}

.ModalCloseButton{
  position: absolute;
  top: 5;
  left:5;
  border: none;
  background: none;
  cursor: pointer;
}

.ModalCloseButton:hover{
  opacity: 0.5;
  transform: scale(1.2);
}

.ModalTitle{
  text-align: center;
}

.EventAddForm{
  display: flex;
  justify-content: center;
}

.editForm{
  display: flex;
  justify-content: center;
}

.BulkRegistationForm{
  display: flex;
  justify-content: center;
}

.FormItem{
  margin: 35px auto;
}

.FormItem label{
  display: inline-block;
  text-align: right;
  width: 80px;

}

.FormItem input{
  padding:10px;
  margin-left: 20px;
  border:none;
  border-bottom: 1px solid skyblue;
  width: 200px;
  transition:0.3s;
}

.FormItem input:focus{
  outline: none;
  transform: scale(1.1);
  background-color: lightsteelblue;
  border-bottom: 3px solid skyblue;
  opacity: 0.6;
}


.FormItem button{
  width: 100px;
  height: 50px;
  display: block;
  margin: auto;
  border: 1px solid dodgerblue;
  background-color: white;
  color:dodgerblue;
  font-size: 17px;
  border-radius: 5px;
  transition: 0.5s;
  cursor: pointer;
}

.FormItem button:hover{
  border: 1px solid white;
  background-color: dodgerblue;
  color: white;
  font-size: 17px;
  border-radius: 5px;
}

.FormItem span{
  padding:10px;
  margin-left: 20px;
  border:none;
  width: 200px;
}


.DeleteEditModal{
  display: flex;
  justify-content: center;
  margin: 50px auto;
}



.deletebutton{
  width: 50%;
  text-align: center;
}

.deletebutton button{
  width: 100px;
  height: 50px;
  border:none;
  background-color: lightpink;
  color: white;
  font-size: 17px;
  border-radius: 5px;
  cursor: pointer;
}

.deletebutton button:hover{
  background-color:tomato;
}

.editbutton{
  width: 50%;
  text-align: center;
}

.editbutton button{
  width: 100px;
  height: 50px;
  border:none;
  background-color:seagreen;
  color: white;
  font-size: 17px;
  border-radius: 5px;
  cursor: pointer;
}

.editbutton button:hover{
  background-color: darkgreen;
}

.eventView{
  text-align: center;
  width: 40%;
  margin: 20px auto;
  padding: 20px;
}

.eventViewItem{
  margin: 20px 0;
}

.modalCalendar{
  font-size: 10px;
  margin:20px auto;
}

.daylist{
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap:5px;
  margin: 10px;
  padding-left: 0;
}
.daylist li{
  background-color: lightgreen;
  opacity: 0.7;
  padding: 5px;
  width: 15%;
  text-align: center;
}


@media (orientation:portrait){
  .Modal {
    top: 10%;
    left: 10%;
    right: 10%;
    bottom: 10%;
    padding: 20px;
  }
  .ModalTitle{
    font-size: 15px;
  }
  .FormItem label{
    font-size: 12px;
    width: 50px;
  }
  .FormItem{
    margin: 35px auto 50px auto;
  }
  .FormItem input{
    padding:5px;
  }

  .eventView{
    text-align: center;
    width: 80%;
  }

  .DeleteEditModal p{
    font-size: 10px;
  }


  .fc-header-toolbar{
    display: flex;
    flex: 1 1 1;
    justify-content: center;
    align-items: center;
    gap:0;
    width: 100%;
  }
  .fc-toolbar-chunk{
    width: 33%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    font-size:8px;
  }
  .fc-theme-standard td, .fc-theme-standard th {
    height: 50px;
  }
  .fc-theme-standard th{
    height: 20px;
    background-color: skyblue;
    color: white;
  }
  .fc-daygrid-event-harness{
    text-align: center;
    padding: 5px;
  }
  
  /*
  .fc-daygrid-event-harness a{
    display: block;
    height: 100%;
  }
  */
  .fc-event-title{
    font-size: 10px;
  }
  .BulkRegistationForm .FormItem{
    margin: 20px auto;
  }

  .daylisttitle{
    font-size: 10px;
  }

  .daylist{
    font-size: 10px;
    margin: 0;
  }

  .daylist li{
    width: 25%;
  }
}

